/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-create-block-gutenberg-draggable-images {
  border: 1px dotted #f00; }

.gutenberg-draggable-images__notices {
  display: flex;
  flex-flow: column; }

.gutenberg-draggable-images__notice-example {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 20px 0; }

.gutenberg-draggable-images__notice-example img {
  width: 50%; }

.gutenberg-draggable-images__notice-example span {
  cursor: pointer; }
